.messageRow {
	display: flex;
}

.messageRowRight {
	display: flex;
	justify-content: flex-end;
}

.messageBlue {
	position: relative;
	margin-left: 20px;
	margin-bottom: 10px;
	padding: 15px;
	background-color: #a8ddfd;
	width: 60%;
	/* height: 50px; */
	text-align: left;
	font: 400 0.9em "Open Sans", sans-serif;
	border: 1px solid #97c6e3;
	border-radius: 10px;
}

.messageBlue::after {
	content: "";
	position: absolute;
	width: 0;
	height: 0;
	border-top: 15px solid #a8ddfd;
	border-left: 15px solid transparent;
	border-right: 15px solid transparent;
	top: 0;
	left: -15px;
}

.messageBlue::before {
	content: "";
	position: absolute;
	width: 0;
	height: 0;
	border-top: 17px solid #97c6e3;
	border-left: 16px solid transparent;
	border-right: 16px solid transparent;
	top: -1px;
	left: -17px;
}

.messageOrange {
	position: relative;
	margin-right: 20px;
	margin-bottom: 10px;
	padding: 15px;
	background-color: #f8e896;
	width: 60%;
	/* height: 50px; */
	text-align: left;
	font: 400 0.9em "Open Sans", sans-serif;
	border: 1px solid #dfd087;
	border-radius: 10px;
}

.messageOrange::after {
	content: "";
	position: absolute;
	width: 0;
	height: 0;
	border-top: 15px solid #f8e896;
	border-left: 15px solid transparent;
	border-right: 15px solid transparent;
	top: 0;
	right: -15px;
}

.messageOrange::before {
	content: "";
	position: absolute;
	width: 0;
	height: 0;
	border-top: 17px solid #dfd087;
	border-left: 16px solid transparent;
	border-right: 16px solid transparent;
	top: -1px;
	right: -17px;
}

.messageContent {
	padding: 0;
	margin: 10px;
}

.messageTimeStampRight {
	position: absolute;
	font-size: 0.65em;
	/* font-weight: 100; */
	margin-top: 10px;
	bottom: 3px;
	right: 5px;
}

.orange {
	color: #ff5722;
	background-color: #ff5722;
	width: 40px;
	height: 40px;
}

.avatarNothing {
	color: transparent;
	background-color: transparent;
	width: 40px;
	height: 40px;
}

.displayName {
	margin-left: 20px;
}
